body{
    background-color: black;
    color: white;
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: 10%;
    margin-right: 10%;
    font-family: sans-serif;
}

.hidden {
    display: none;
}
.flexbox{
    display: flex;
}

img {
    image-rendering: crisp-edges;
}

.pokemon-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 50px;
}

.card{
    background-color: rgb(19, 19, 19);
    width: 250px;
    border-radius: 5px;
    position: relative;
    text-align: center;
}

.card-title {
    text-transform: capitalize;
    margin: 0;
    margin-top: 30px;
    font-size: 20px;
}

.num {
    position: absolute;
    margin: 0px;
    top: 10px;
    left: 10px;
}

.card-img {
    text-align: center;
    margin: 10px;
}

.modal {
    position: fixed;
    background-color: rgb(36, 36, 36);
    width: 700px;
    height: 600px;
    border-radius: 5px;
    text-align: center;
    margin: auto;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.closer {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
}
.closer:hover {
    opacity: 50%;
    transition: 200ms;
}

.modal-header{
    border-bottom: solid rgb(19, 19, 19);
    align-items: center;
    justify-content: space-between;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
    gap: 0;
}

.modal-img {
    width: 200px;
    background-color: rgb(19, 19, 19);
    border-radius: 5px;
}

.modal-title-text {
    font-size: 35px;
    text-transform: capitalize;
    text-align: right;
}

.modal-description h2, .modal-description p{
    text-align: left;
    margin-left: 50px;
    font-weight: bolder;
}

#types, #abilities {
    align-items: center;
}

.type {
    height: 20px;
    margin-left: 20px;
}


.ability {
    margin-left: 20px;
    text-transform: capitalize;
    color: rgb(169, 255, 169)
}

.stats {
    margin-top: 15px;
    font-size: 10px;
}

.stat-bars {
    margin-top: 15px;
}

.stat-bar {
    background-color: aqua;
    margin-top: 11.6px;
    margin-left: 20px;
    height: 10px;
}

.attr {
    position: absolute;
    left: 200px;
}

.height, .weight {
    color: rgb(255, 148, 148)
}


@media (max-width: 730px){
    .modal {   
        width: 350px;
        height: 400px;
    }
    
    .modal-header{
        padding-left: 25px;
        padding-right: 25px;
    }
    
    .modal-img {
        width: 100px;
    }
    
    .modal-title-text {
        font-size: 18px;
    }
    
    .modal-description h2, .modal-description p{
        margin-left: 12px;
    }
    .modal-description h2 {
        font-size: 15px;
    }

    .type {
        height: 20px;
        margin-left: 10px;
    }
    
    
    .ability {
        margin-left: 10px;
        font-size: 12px;
    }
    
    .stats {
        margin-top: 8px;
        font-size: 10px;
    }
    
    .stat-bars {
        margin-top: 8px;
    }
    
    .stat-bar {
        background-color: aqua;
        margin-top: 11.6px;
        margin-left: 5px;
        height: 10px;
    }
    
    .attr {
        position: absolute;
        left: 120px;
    }
    
    #attributes h2{
        font-size: 15px;
    }
}